.yagodi-promo {
  position: relative;
  margin-bottom: 44px;
  height: 370px;
  background: url(../i/yagodi-promo.jpg) no-repeat 50% 50%;
  background-size: cover; }

.yagodi-promo:after {
  position: absolute;
  right: 0;
  bottom: -14px;
  left: 0;
  display: block;
  height: 22px;
  background: url(../i/yagodi-decor.png) repeat-x 0 0;
  content: ''; }

.yagodi-promo-logo {
  position: absolute;
  top: 55px;
  left: 0;
  overflow: hidden;
  -webkit-transition: all 1s ease-in-out;
  -moz-transition: all 1s ease-in-out;
  transition: all 1s ease-in-out;
  z-index: 2;
  margin-left: -391px;
  width: 391px;
  height: 209px;
  background: url(../i/yagodi-promo-logo.png) no-repeat;
  opacity: 0; }

.yagodi-promo-logo:before {
  content: "";
  display: block;
  width: 0;
  height: 100%; }

.animated .yagodi-promo-logo {
  left: 50%;
