.berryfall {
  position: relative;
  overflow: hidden;
  margin: 0 auto;
  width: 942px;
  height: 370px;
  background: url(../i/berryfall.png) no-repeat 100% 0; }

.berry-1, .berry-2, .berry-3 {
  -webkit-animation-name: fall;
  -moz-animation-name: fall;
  animation-name: fall;
  -webkit-animation-timing-function: ease-in;
  -moz-animation-timing-function: ease-in;
  animation-timing-function: ease-in;
  -webkit-animation-iteration-count: infinite;
  -moz-animation-iteration-count: infinite;
  animation-iteration-count: infinite;
  position: absolute;
  top: -150px;
  width: 150px;
  height: 150px;
  background-position: 50% 50%;
  background-repeat: no-repeat; }

.berry-1 {
  -webkit-animation-duration: 7s;
  -moz-animation-duration: 7s;
  animation-duration: 7s;
  -webkit-animation-delay: 0.5s;
  -moz-animation-delay: 0.5s;
  animation-delay: 0.5s;
  left: 380px;
  background-image: url(../i/berry-1.png); }

.berry-2 {
  -webkit-animation-duration: 5s;
  -moz-animation-duration: 5s;
  animation-duration: 5s;
  -webkit-animation-delay: 0.5s;
  -moz-animation-delay: 0.5s;
  animation-delay: 0.5s;
  left: 630px;
  background-image: url(../i/berry-2.png); }

.berry-3 {
  -webkit-animation-duration: 8s;
  -moz-animation-duration: 8s;
  animation-duration: 8s;
  -webkit-animation-delay: 1.5s;
  -moz-animation-delay: 1.5s;
  animation-delay: 1.5s;
  left: 770px;
  background-image: url(../i/berry-3.png); }

@-webkit-keyframes fall {
  0 {
    top: -150px; }
  50% {
    top: 370px; }
  100% {
    top: 370px; } }

@-moz-keyframes fall {
  0 {
    top: -150px; }
  50% {
    top: 370px; }
  100% {
    top: 370px; } }

@keyframes fall {
  0 {
    top: -150px; }
  50% {
    top: 370px; }
  100% {
    top: 370px; } }

.yagodi-pack-container {
